<template>
  <div class="content">
    <div class="banaer">
    <div class="container">
    <div class='head'>我的收藏</div>
    <el-table :data="tableData"style="width: 100%">
      <el-table-column label="漫画">
          <template slot-scope="scope">
            <a @click="goLink(scope.row , '/detail')">{{ scope.row.name }}</a>
          </template>
      </el-table-column>
      <el-table-column label="作者" prop="author">
      </el-table-column>
      <el-table-column label="分类" prop="tag">
      </el-table-column>
      
      <el-table-column label="发布时间" width="156" getLocalTime :formatter="dateFormat" prop="date">
      </el-table-column>
      <el-table-column label="更新章节"  width="80" prop="chapter" align="center">
      </el-table-column>
      <el-table-column label="评分"  width="50" prop="rate">
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-popover ref="popover{{$index}}" class="aew" placement="top" >
            <p>删除 {{ scope.row.title }} ？</p>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="cancel()">取消</el-button>
              <el-button size="mini" type="danger" round @click="handleDelete(scope.$index, scope.row)">确定</el-button>
            </div>
          </el-popover>
          <el-button size="mini" type="danger" v-popover:popover{{$index}} ><i class="el-icon-delete"/></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pager">
      <el-pagination layout="prev, pager, next" :current-page="currentPage" :total=totalCount @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>
  </div>
  </div>
  </div>
</template>

<script type="text/ecmascript-6">
import {mapMutations} from 'vuex'
export default {
  data() {
    return {
      searchParam: {},
      param:{},
      currentPage: 1,
      totalCount: 0,
      tableData: []
    }
  },
  created() {
    this.searchParam.param = JSON.parse(sessionStorage.getItem('u')).uid
    this.getData()
  },
  methods: {
    handleCurrentChange(val) {
      this.searchParam.page = val-1
      this.currentPage = val
      this.getData()
    },
    handleDelete(index, row) {
      this.param.cid = row.cid
      if(JSON.parse(sessionStorage.getItem('u')) === null || JSON.parse(sessionStorage.getItem('u')).uid === undefined){
        this.$router.push('/login')
      }else{
        this.param.uid = JSON.parse(sessionStorage.getItem('u')).uid
        document.querySelector("#app").click()
        var _this = this
        this.$ajax.post('/api/removeFavorite',_this.param).then(function(response){
          if(response.data.code === 0){
            _this.$message({message: response.data.data,type: 'success'})
            _this.getData()
          }else{
            _this.$message({message: response.data.msg,type: 'error'})
          }
        }).catch(function(error){console.log(error)})
      }
    },
    dateFormat(row, column) {  
      return this.getDateTime(row[column.property])
    },
    getData() {
      var _this = this
      this.$ajax.post('/api/getFavorites',_this.searchParam).then(function(response){
        _this.tableData = response.data.data.content
        _this.totalCount = response.data.data.totalElements
      }).catch(function(error){console.log(error)})
    },
    goLink( row, path) {
      this.$router.push({ 
        path: path,
        query:{cid:row.cid}
      })
    },
    cancel() {
      document.querySelector("#app").click()
    }
  }
}
</script>

<style lang='stylus' rle='stylesheet/stylus'>
.banaer
  padding-top 10px
  // background url(../images/new/m_banner_bak1.png)
  background-position top center
  background-repeat no-repeat
  min-width 1000px
  .container
    width 1000px
    margin 0 auto
    overflow hidden
    position relative
    .userCenterBar
      background-color #fff
      margin 20px 0
a
  text-decoration none
a:hover
  cursor pointer
  color #ff3860
.head
  font-size 18px
  margin-bottom 14px
.el-tag--info
  border-color #fff
.el-pagination .btn-next, .el-pagination .btn-prev, .el-pagination
  color #909399
.el-pager li.active
  color	#FFB6C1
.pager
  margin 35px
  text-align center
</style>



